}

.game-item div {
  opacity: 0;
}

.game-item:hover {
  text-decoration: none;
  border-color: rgb(36, 37, 41);
  border-style: groove;
  color: rgb(36, 37, 41);
  width: 320px;
  height: 300px;
  display: block;
  border-radius: 15px;
  cursor: url(data:image/png;base64,)
      14 0,
    pointer;
